import { Component, OnInit, Inject, ViewChild } from '@angular/core';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material/dialog';
import {CdkTextareaAutosize} from '@angular/cdk/text-field';
import { StikynotService } from './stikynot.service';
import { Nota } from './nota';

export interface DialogData {
  titulo: string;
  message: string;
  color: string; 
}

@Component({
  selector: 'app-stikynot',
  templateUrl: './stikynot.component.html',
  styleUrls: ['./stikynot.component.css']
})
export class StikynotComponent implements OnInit {

  titulo: string;
  message: string;
  color: string; 

  public lstNotes:Nota[]; 

  constructor(public dialog: MatDialog,private stikynotService: StikynotService) { }

  @ViewChild('autosize') autosize: CdkTextareaAutosize;

  openDialog(): void {
    const dialogRef = this.dialog.open(DialogOverviewExampleDialog, {
      panelClass: 'full-width-dialog',
      data: {titulo: this.titulo, message: this.message, color: this.color}
    });

    dialogRef.afterClosed().subscribe(result => {
      console.log(result);
      this.titulo = result.titulo;
      this.message = result.message;
      this.color = result.titulo;
    });
  }

  ngOnInit(): void {
    this.stikynotService.list().subscribe(data => {
      this.lstNotes = data;
     });
  }

}

@Component({
  selector: 'dialog-overview-example-dialog',
  templateUrl: './dialog-overview-example-dialog.html'
})
export class DialogOverviewExampleDialog {

  constructor(
    public dialogRef: MatDialogRef<DialogOverviewExampleDialog>,
    @Inject(MAT_DIALOG_DATA) public data: DialogData) {}

  onNoClick(): void {
    this.dialogRef.close();
  }

}
